.payment {
  background-color: white;

  &__container {
    h1 {
      text-align: center;
      padding: 10px;
      font-weight: 400;
      background-color: rgb(234, 237, 237);
      border-bottom: 1px solid lightgray;

      a {
        text-decoration: none;
      }
    }

    &__section {
      display: flex;
      padding: 20px;
      margin: 0 20px;
      border-bottom: 1px solid lightgray;

      &__title {
        flex: 0.2;
      }

      &__address,
      &__items,
      &__details {
        flex: 0.8;
      }

      &__details {
        form {
          max-width: 400px;

          div button {
            background: #f0c14b;
            border-radius: 2px;
            width: 100%;
            height: 30px;
            border: 1px solid;
            font-weight: bolder;
            margin-top: 10px;
            border-color: #a88734 #9c7e31 #846129;
            color: #111;
          }
        }

        h3 {
          padding-top: 8px;
          padding-bottom: 20px;
        }
      }
    }
  }
}
